.ionic a{
    display:inline-block;
    height:2.5rem;
    width:2.5rem;
    background-color:#5356fd;
    background-image: linear-gradient(#5356, #8943F4);
    color:#fff!important;
    border-radius:100%;
    text-align:center;
    font-size:1.5rem;
    line-height:2.5rem;
     margin-left:1.5rem; 
     margin-top: 1rem;
  }
  .ionic a:last-child{margin-right:0}
  .ionic a:hover{
    background-color:#002bdc;
    background-image: linear-gradient(to bottom right,#002bdc, #8943F4)
  }
  .ionic{
      margin-right:0.5rem;
      text-align: center;
  } 