  .social-icons a{
    display:inline-block;
    height:3.5rem;
    width:3.5rem;
    background-color:#09cebd;
    background-image: linear-gradient(#09cebd);
    color:#fff!important;
    border-radius:100%;
    text-align:center;
    font-size:1.5rem;
    line-height:3.5rem;
    margin-right:1rem
  }
  .social-icons a:last-child{margin-right:0}
  .social-icons a:hover{
    background-color:#09cebd;
    background-image: linear-gradient(to bottom right,#09cebd,#259289)
  }
  .project{
    background:#d3f6f3 ;
  }

    
  